import axios from 'axios'

import {API_V1_URL} from '@/config'

function apiToUpload(data) {
  return {
    id: data.id,
    status: data.status,
    filename: data.meta.fileName,
    time: null, // TODO
    errorMsg: data.output ? data.output.reason : null,
  }
}

export default {
  uploadCSV(csv) {
    return axios.post(API_V1_URL + '/tx/bulk', csv)
      .then(res => res.data.data)
  },
  getRecentUploadsSummary() {
    return axios.get(API_V1_URL + '/operations')
      .then(res => res.data.data.map(upload => apiToUpload(upload)))
  },
}



