<template>
  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Time</th>
        <th>ID</th>
        <th>File</th>
        <th>Status</th>
        <th>Notes</th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="upload in uploads">
        <td>{{ upload.time }}</td>
        <td>{{ upload.id }}</td>
        <td>{{ upload.filename }}</td>
        <td>{{ upload.status }}</td>
        <td>{{ upload.errorMsg }}</td>
      </tr>
    </tbody>

  </table>
</template>


<script>
// import userService from '@/services/usersService'

export default {
  props: {
    uploads: {},
  },
}
</script>



